:root {
  --bodyBackgroundColor: #ebebeb;
  --bodyFontFamily: sans-serif;
  --bodyLineHeight: 1.5;
}

html {
  height: 100%;
}

body {
  background-color: var(--bodyBackgroundColor);
  font-family: sans-serif;
  line-height: 1.5;
  margin: 0;
  min-height: 100%;
}



